import React from "react";
import SearchBar from "./search2";
import RentalFilter from "./filter";
import Header from "../header";
import Button from "../button";
import Footer from "../footer";

import "../../css/rental/result.css";
import "../../css/rental/searchbar/result.css";

class RentalResult extends React.Component {
  constructor(props) {
    super(props);

    this._renderItems = this._renderItems.bind(this);
    this._costSort = this._costSort.bind(this);
    this._numberSort = this._numberSort.bind(this);
  }

  render() {
    return (
      <div>
        <Header />
        <div className="rentalresult">
          <section>
            <div className="s2_search_wrapper">
              <div className="s2_title">搜尋</div>
              <SearchBar setRentalSearch={this.props.setRentalSearch} />
            </div>
            <RentalFilter
              filter={this.props.filter}
              setRentalFilter={this.props.setRentalFilter}
            />
          </section>
          <section className="container">
            <div className="countresult">
              找到 {this.props.result.length} 筆結果
            </div>
            <div>
              <label>
                <input
                  name="價格"
                  type="checkbox"
                  onChange={() => {
                    this._costSort(0);
                  }}
                />
                價格由低到高
              </label>
              <label>
                <input
                  name="價格"
                  type="checkbox"
                  onChange={() => {
                    this._costSort(1);
                  }}
                />
                價格由高到低
              </label>
            </div>
            <div>
              <label>
                <input
                  name="人數"
                  type="checkbox"
                  onChange={() => {
                    this._numberSort(0);
                  }}
                />
                人數由低到高
              </label>
              <label>
                <input
                  name="人數"
                  type="checkbox"
                  onChange={() => {
                    this._numberSort(1);
                  }}
                />
                人數由高到低
              </label>
            </div>
            <div>{this._renderItems()}</div>
          </section>
        </div>
        <Footer />
      </div>
    );
  }

  _numberSort(type) {
    const { result } = this.props;
    let r = result;
    if (!type) {
      r.sort((a, b) => {
        return a.number - b.number;
      });
    } else {
      r.sort((a, b) => {
        return b.number - a.number;
      });
    }
    this.props.setRentalResult(r);
  }

  _costSort(type) {
    const { result } = this.props;
    let r = result;
    if (!type) {
      r.sort((a, b) => {
        return a.cost - b.cost;
      });
    } else {
      r.sort((a, b) => {
        return b.cost - a.cost;
      });
    }
    this.props.setRentalResult(r);
  }

  _renderItems() {
    const { result } = this.props;
    let list = [];
    if (result !== []) {
      result.forEach((item, index) => {
        let t = [];
        item.type.forEach((titem) =>
          t.push(
            <div className="type" key={titem}>
              {titem}
            </div>
          )
        );
        list.push(
          <div className="card" key={index}>
            <div className="district">{item.district}</div>
            <div className="img"></div>
            <div className="info">
              <h4>{item.name}</h4>
              <div className="flex">{t}</div>
              <div className="other">
                <div className="icon"></div>{item.number} 人
              </div>
              <div className="other">
                <div className="icon"></div>{item.cost} 元 / hr
              </div>
              <div className="other">
                <div className="icon"></div>{item.device}
              </div>
            </div>
            <Button className="button" type={1} nextPath={"/VenueDetail"} text={"線上租借"}/>
          </div>
        );
      });
    } else {
      // 找不到
    }
    return list;
  }
}

export default RentalResult;
